.Header{
    background-image: url(https://tinder.com/static/build/07eef8b101ca939c978a599f508aa955.webp);
    width: 100%;
    height: 750px;
    overflow: hidden;
    background-size: cover;
    
}
.line{
    width: 100px;
    height: 110px;
}
.picture{
    position: relative;
    left: 0px;
    height: 100px;
    width: 170px;
    background-position: 10px 0px;
    background-repeat: no-repeat;
}
img{
    width: 100%;
    margin: 0 0 -50% -20%;
}
